import { chatListState } from "../const/chatList";

const seenMessageReducer = (
  state = chatListState,
  action: { type: string; id: number }
) => {
  switch (action.type) {
    case "SEEN_CHAT":
      const newChatList = [...state.chatList];
      const currentChat = newChatList.findIndex(
        (chat) => chat.id === action.id
      );
      newChatList[currentChat].chats.forEach((chat) => {
        chat.seen = true;
      });
      newChatList[currentChat].countUnseen = 0;
      const countUnseenChat = newChatList.reduce((count, chat) => {
        return count + chat.countUnseen;
      }, 0);

      return { ...state, chatList: newChatList, countUnseenChat };
    default:
      return state;
  }
};

export default seenMessageReducer;
