import { Nav, Badge } from "react-bootstrap";
import { useSelector } from "react-redux";
import { NavLink } from "react-router-dom";
import { RootState } from "../../redux/reducer";
import "./index.scss";

function NavHeader() {
  const countUnseenMessage = useSelector((state: RootState) => state.chatListState?.countUnseenChat)
  return (
    <Nav className="mr-auto nav-header">
      <Nav.Link exact as={NavLink} to="./">
        DashBoard
      </Nav.Link>
      <Nav.Link as={NavLink} to="/projects">
        Projects
      </Nav.Link>
      <Nav.Link as={NavLink} to="/messages" className="position-relative">
        Messages
        <Badge className="mess-count position-absolute" variant="danger">
          {countUnseenMessage}
        </Badge>
      </Nav.Link>
    </Nav>
  );
}
export default NavHeader;
