import { useState } from "react";
import { Badge, Image, Nav } from "react-bootstrap";
import { useDispatch, useSelector, useStore } from "react-redux";
import { NavLink } from "react-router-dom";
import { RootState } from "../../redux/reducer";
import "./index.scss";

function Notify() {
  const countNotify = useSelector(
    (state: RootState) => state.notifiesState.countUnseenNotifies
  );
  const dispatch = useDispatch();
  // const handleSeen = (id: number) => {
  //   dispatch(seenNotify(id));
  // };
  return (
    <Nav.Link
      as={NavLink}
      to="/notifications"
      className="position-relative notify"
    >
      <Image src="./img/bell-o.svg" alt="notify" />
      <Badge className="notify-count position-absolute" variant="danger">
        {countNotify}
      </Badge>
    </Nav.Link>
  );
}

export default Notify;
