import { Image, ListGroup } from "react-bootstrap";
import { connect, useDispatch, useSelector } from "react-redux";
import { seenNotify } from "../../redux/action/notify";
import { RootState } from "../../redux/reducer";
import "./index.scss";

interface Notify {
  id: number;
  content: string;
  seen: boolean;
}

function Notifications() {
  const notifies = useSelector(
    (state: RootState) => state.notifiesState.notifies
  );
  const dispatch = useDispatch();
  const handleSeen = (id: number) => {
    dispatch(seenNotify(id));
  };

  // ==============RENDER===========
  return (
    <div className="page-notify ">
      <ListGroup variant="flush" className="page-notify__content bg-white">
        {notifies &&
          notifies.map(function (notify: any, index: any) {
            return (
              <ListGroup.Item
                action
                key={index}
                variant={notify.seen ? "secondary" : "light"}
                onClick={() => {
                  handleSeen(notify.id);
                }}
              >
                <div className="d-flex align-items-center  ">
                  <Image src="./img/0.png" width="44" height="44" />
                  <span className="name ml-2 mr-auto">{notify.content}.</span>
                  <span className="text-secondary recent-date ">1 day ago</span>
                </div>
              </ListGroup.Item>
            );
          })}
      </ListGroup>
    </div>
  );
}

export default Notifications;
