import { notifiesState } from "../const/notify";

const seenNotifyReducer = (state = notifiesState, action: any) => {
  switch (action.type) {
    case "SEEN":
      let { notifies, countUnseenNotifies } = state;
      const newNotifies = [...notifies];
      const seenNotify = newNotifies.find((notify) => notify.id === action.id);
      if (seenNotify) {
        seenNotify.seen = true;
      }

      countUnseenNotifies = newNotifies.filter(
        (notify) => notify.seen === false
      ).length;
      return { ...state, notifies: newNotifies, countUnseenNotifies };
    default:
      return state;
  }
};

export default seenNotifyReducer;
