import { useState } from "react";
import { Button, Form, Image, ListGroup, Tab } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import Search from "../../components/seach-box";
import { seenMessage } from "../../redux/action/seenMessage";
import { RootState } from "../../redux/reducer";
import "./index.scss";

function Messages() {
  const myID = 10;
  const chatsList = useSelector(
    (state: RootState) => state.chatListState.chatList
  );
  const dispatch = useDispatch();
  const [message, setMessage] = useState<string>("");
  const [currentChat, setCurrentChat] = useState<any>(
    localStorage.getItem("currentChat") || null
  );

  const handleTyping = function (e: any) {
    setMessage(e.target.value);
  };
  const onSend = function () {
    const newChatList = [...chatsList];
    let chat = newChatList.find(function (chat) {
      return chat.id === currentChat;
    });
    chat?.chats.push({
      id: myID,
      content: message,
      seen: false,
    });
    dispatch(newChatList);
    setMessage("");
  };

  const search = (value: any) => {
    if (!value) {
      // setChatList(chatsList);
    }
    const newChats = chatsList.filter((chat) => chat.receiver === value);
    // setChatList(newChats);
  };
  const setChat = (id: any) => {
    setCurrentChat(id);
    localStorage.setItem("currentChat", id);
  };
  return (
    <div className="page-message ">
      <Tab.Container
        id="list-group-tabs-example"
        defaultActiveKey={"chat" + currentChat}
      >
        <div className="d-flex flex-column flex-lg-row justify-content-between">
          <div className="page-message__list">
            <div className="d-flex justify-content-between direct-message">
              <span>
                Direct Message
                <Image src="./img/angle-right.svg" />
              </span>
              <span>
                <Image src="./img/new message icon.svg" alt="new mess" />
              </span>
            </div>

            <div className="mt-2 mb-2">
              <Search search={search} />
            </div>
            <ListGroup variant="flush">
              {chatsList &&
                chatsList.map(function (chat) {
                  return (
                    <ListGroup.Item
                      active={chat.id === currentChat}
                      action
                      eventKey={"chat" + chat.id}
                      key={chat.id}
                      onClick={() => {
                        setChat(chat.id);
                        dispatch(seenMessage(chat.id));
                      }}
                      variant={chat.countUnseen === 0 ? "secondary" : "light"}
                    >
                      <div className="list-item d-flex">
                        <div className="flex-grow-0 avatar">
                          <Image src="./img/0.png" width="44" height="44" />
                        </div>
                        <div className="flex-grow-1 ml-2">
                          <div className="d-flex justify-content-between">
                            <h6 className="name">{chat.receiver}</h6>
                            <span className="text-secondary recent-date">
                              1 day ago
                            </span>
                          </div>
                          <div className="text-secondary mw-100 width-200 text-truncate">
                            {chat.chats[chat.chats.length - 1].content}
                          </div>
                        </div>
                      </div>
                    </ListGroup.Item>
                  );
                })}
            </ListGroup>
          </div>
          <div className="flex-item message-detail">
            <Tab.Content>
              {chatsList &&
                chatsList.map(function (chat) {
                  return (
                    <Tab.Pane
                      className="message-tab"
                      eventKey={"chat" + chat.id}
                      key={chat.id}
                    >
                      <div className="message-detail__info">
                        <div className="d-flex justify-content-between">
                          <div>
                            <Image src="./img/0.png" alt="avatar" />
                            <span className="message-detail__username">
                              {chat.receiver}
                            </span>
                          </div>
                          <span>
                            <Image src="./img/phone.svg" alt="phone" />
                          </span>
                        </div>
                      </div>
                      <div className="message-detail__content overflow-auto">
                        {chat.chats.map((mess) =>
                          mess.id === myID ? (
                            <div
                              key={Math.random()}
                              className="my-message text-right text-break  "
                            >
                              <p className="d-inline-block bg-blue ml-5 mw-75 ">
                                {mess.content}
                              </p>
                            </div>
                          ) : (
                            <div
                              key={Math.random()}
                              className=" text-left receive-message"
                            >
                              <Image src="./img/0.png" alt="avatar" />
                              <p className="d-inline-block mr-1">
                                {mess.content}
                              </p>
                            </div>
                          )
                        )}
                      </div>
                      <div className="message-detail__custom">
                        <div className="d-flex justify-content-between">
                          <div>
                            <Image src="./img/paperclip icon.svg" />
                          </div>
                          <div className="ml-4 w-100 ">
                            <Form.Control
                              as="textarea"
                              rows={1}
                              className="border-radius-20"
                              type="text"
                              placeholder="Start typing a message..."
                              onChange={handleTyping}
                              value={message}
                            />
                          </div>
                          <Button
                            onClick={onSend}
                            type="submit"
                            className="btn-send"
                          >
                            Send
                          </Button>
                        </div>
                      </div>
                    </Tab.Pane>
                  );
                })}
            </Tab.Content>
          </div>
        </div>
      </Tab.Container>
    </div>
  );
}

export default Messages;
